const cartSelector = {
  state: {
    selectedProducts: []
  },
  getters: {
    selectedProducts (state) {
      return state.selectedProducts
    },
    allSelected (state, getters) {
      for (let i = 0; i < getters.cartProducts.length; i++) {
        if (!state.selectedProducts.includes(i)) {
          return false
        }
      }
      return true
    },
    itemCount (state, getters) {
      return state.selectedProducts.reduce((count, idx) => count + getters.cartProducts[idx].quantity, 0)
    },
    isSelected (state) {
      return function (index) {
        return state.selectedProducts.includes(index)
      }
    }
  },
  mutations: {
    handleCartSelect (state, index) {
      state.selectedProducts.push(index)
    },
    handleCartDeselect (state, index) {
      state.selectedProducts = state.selectedProducts.filter(i => i !== index)
    },
    handleAllSelect (state, getters) {
      state.selectedProducts = getters.cartProducts.map((product, index) => index)
    },
    handleAllDeselect (state) {
      state.selectedProducts = []
    },
    shiftAfter (state, index) {
      for (let i = 0; i < state.selectedProducts.length; i++) {
        if (state.selectedProducts[i] > index) {
          state.selectedProducts[i]--
        }
      }
    }
  },
  actions: {
    handleCartSelect (context, index) {
      context.commit('handleCartSelect', index)
    },
    handleCartDeselect (context, index) {
      context.commit('handleCartDeselect', index)
    },
    handleAllSelect (context) {
      context.commit('handleAllSelect', context.getters)
    },
    handleAllDeselect (context) {
      context.commit('handleAllDeselect')
    },
    handleCartRemove (context, index) {
      context.dispatch('cartRemove', [index])
        .then(response => {
          context.commit('handleCartDeselect', index)
          context.commit('shiftAfter', index)
        })
    },
    handleDeleteSelected (context) {
      const indices = context.getters.selectedProducts
      context.commit('handleAllDeselect')
      context.dispatch('cartRemove', indices).then()
    }
  }
}

export default cartSelector
