<template>
  <div>
    <b-form @submit="search">
      <b-input-group class="mr-2">
        <!-- <b-input-group-prepend is-text>
          <b-icon size="lg" icon="search"></b-icon>
        </b-input-group-prepend> -->
        <span class="d-block d-lg-none">
          <b-form-input type="search" squared v-model="searchString" :id="'tarf'+id" placeholder="" autocomplete="off"></b-form-input>
        </span>
        <span class="d-none d-lg-block">
          <b-form-input type="search" v-model="searchString" :id="'tar'+id" placeholder="" autocomplete="off" style="width:550px;"></b-form-input>
        </span>
        <b-input-group-prepend style="background-color:orange;">
          <b-button type="submit" @click="search" variant="white" :disabled="searchString === ''">
            <b-icon size="lg" icon="search" variant="" :scale="searchString === '' ? 1.2 : 1.4"></b-icon>
          </b-button>
        </b-input-group-prepend>
      </b-input-group>
      <span class="text-light" to="/flash-sale">Deals</span>
                <span class="d-lg-none"><showBrands></showBrands></span>
                      <b-button class="text-light ml-4" variant="">Brands</b-button>
                      <b-button class="text-light ml-4" variant="">Brands</b-button>
    </b-form>
    <span class="d-block d-lg-none">
      <b-popover v-if="searchString !== ''" :show.sync="show" :target="'tarf'+id" triggers="focus" ref="popover" placement="bottom">
        <b-list-group>
          <b-list-group-item v-for="(match, index) in matchedNames" :key="index" :to="getLink(match.item)" @click="show=false;searchString=''">
            <!-- <router-link :to="getLink(match.item)"> -->
            {{match.item.name}}
            <!-- </router-link> -->
        </b-list-group-item>
        </b-list-group>
      </b-popover>
    </span>
    <span class="d-none d-lg-block">
      <b-popover v-if="searchString !== ''" :show.sync="show" :target="'tar'+id" triggers="focus" ref="popover" placement="bottom">
        <b-list-group>
          <b-list-group-item v-for="(match, index) in matchedNames" :key="index" :to="getLink(match.item)" @click="show=false;searchString=''">
            <!-- <router-link :to="getLink(match.item)"> -->
            {{match.item.name}}
            <!-- </router-link> -->
        </b-list-group-item>
        </b-list-group>
      </b-popover>
    </span>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Fuse from 'fuse.js'
import showBrands from './ShowBrands'

export default {
  name: 'SearchBar',
  props: [
    'id'
  ],
  data () {
    return {
      searchString: '',
      show: false
    }
  },
  components: {
    showBrands
  },
  computed: {
    ...mapGetters([
      'searchProducts',
      'suggestionScope'
    ]),
    matchedNames () {
      if (this.searchString === '') {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.show = false
        // this.$refs.popover.$emit('disable')
        return []
      }
      const fuse = new Fuse(this.suggestionScope, {
        keys: [
          'name',
          'category' // only subCategories will have 'category' property
        ]
      })
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.show = true
      // if (this.show === true) this.$refs.popover.$emit('enable')
      return fuse.search(this.searchString).slice(0, process.env.VUE_APP_SEARCH_BAR_LIMIT)
    }
  },
  methods: {
    getLink (item) {
      if (item.type === 'brand') {
        return {
          name: 'CertainBrand',
          query: {
            name: item.name
          }
        }
      } else if (item.type === 'category') {
        return {
          name: 'CertainCategory',
          query: {
            'cat-name': item.name
          }
        }
      } else if (item.type === 'subCategory') {
        return {
          name: 'CertainSubCategory',
          query: {
            'cat-name': item.category,
            'subcat-name': item.name
          }
        }
      }
    },
    search (event) {
      event.preventDefault()
      const searchId = parseInt(this.searchString)
      if (this.searchProducts.find(product => product.id === searchId)) {
        this.$router.push({
          name: 'CertainProduct',
          params: {
            id: this.searchString
          }
        }).catch(() => {})
      } else {
        this.$router.push({
          name: 'SearchOnAll',
          query: {
            string: this.searchString
          }
        }).catch(() => {})
      }
    }
  }
}
</script>

