import api from '../../plugins/axios'

const cart = {
  state: {
    cartProducts: [],
    cartProductsLoaded: false
  },
  getters: {
    cartProducts (state) {
      return state.cartProducts
    },
    cartProductsLoaded (state) {
      return state.cartProductsLoaded
    },
    cartProductCount (state) {
      return state.cartProducts.length.toString()
    }
  },
  mutations: {
    loadCart (state, data) {
      state.cartProducts = data
      state.cartProductsLoaded = true
      sessionStorage.setItem('cartProducts', JSON.stringify(data))
    },
    addToCart (state, products) {
      state.cartProducts = state.cartProducts.concat(products)
      sessionStorage.setItem('cartProducts', JSON.stringify(state.cartProducts))
    },
    cartRemove (state, indices) {
      state.cartProducts = state.cartProducts.filter((product, index) => !indices.includes(index))
      sessionStorage.setItem('cartProducts', JSON.stringify(state.cartProducts))
    },
    clearCart (state) {
      state.cartProducts = []
      state.cartProductsLoaded = false
    }
  },
  actions: {
    loadCart (context, bypass) {
      context.commit('handleAllDeselect')
      const cartProducts = sessionStorage.getItem('cartProducts')
      if (cartProducts && !(context.getters.authToken && bypass)) {
        context.commit('loadCart', JSON.parse(cartProducts))
      } else if (context.getters.authToken) {
        api.get('/cart/products', context.getters.getConfig())
          .then(response => {
            console.log('cart loaded')
            console.log(response)
            context.commit('loadCart', response.data.cartProducts)
          })
          .catch(error => {
            console.log('cart load failed')
            console.log(error.response)
          })
      }
    },
    addToCart (context, products) {
      context.commit('handleAllDeselect')
      if (!context.getters.authToken) {
        products = products.map(product => {
          product.id = product.productID
          delete product.productID
          return product
        })
        context.commit('addToCart', products)
      } else {
        return new Promise((resolve, reject) => {
          // not everything is needed at the api end
          products = products.map(product => {
            return {
              productID: product.productID,
              specifications: product.specifications,
              quantity: product.quantity
            }
          })
          api.post('/cart/add', { products }, context.getters.getConfig())
            .then(response => {
              console.log('added to cart')
              console.log(response)
              context.commit('loadCart', response.data.cartProducts)
              resolve(response)
            })
            .catch(error => {
              console.log('cart add failed')
              console.log(error.response)
              reject(error)
            })
        })
      }
    },
    cartRemove (context, indices) {
      if (!context.getters.authToken) {
        context.commit('cartRemove', indices)
      } else {
        return new Promise((resolve, reject) => {
          api.delete('/cart/remove', context.getters.getConfig(null, { indices }))
            .then(response => {
              console.log('removed from cart')
              console.log(response)
              context.commit('cartRemove', indices)
              resolve(response)
            })
            .catch(error => {
              console.log('cart remove failed')
              console.log(error.response)
              reject(error)
            })
        })
      }
    }
  }
}

export default cart
