<template>
  <div>
    <div aria-controls="sidebar" aria-expanded=""
     @click="show=true" class="text-light" id="category-button">
      <span class="d-flex d-inline"><b-avatar icon="grid" size="sm" variant="dark" class="popup-more"></b-avatar>Brands</span></div>
    <b-sidebar id="sidebar" v-model="show" shadow>
      <template v-slot:title>
        <span class="">Brands</span>
      </template>
      <div class="px-3 py-2">
          <b-list-group-item v-for="(brand, i) in searchBrand" :key="i"  class="d-flex d-inline">
            <b-button id="button1" variant="light" :to="getBrandLink(brand)"
             class="my-auto text-center d-flex justify-content-between">{{brand}}
            </b-button>
          </b-list-group-item>
      </div>
    </b-sidebar>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'ShowCategory',
  data () {
    return {
      show: false
    }
  },
  computed: {
    ...mapGetters([
      'searchBrand'
    ])
  },
  methods: {
    getBrandLink (item) {
      return {
        name: 'CertainBrand',
        query: {
          name: item
        }
      }
    }
  }
}
</script>

<style scoped>
#category-button {
  padding-left: 0;
  cursor: pointer;
}
</style>
