<script>
import { mapActions, mapGetters } from 'vuex'
import SearchBar from './SearchBar'
import nested from '../components/ShowCategory'
import LogIn from './Entry/LogIn'
import SignUp from './Entry/SignUp'

export default {
  name: 'Header',
  data () {
    return {
      show1: false
    }
  },
  computed: {
    ...mapGetters([
      'authToken',
      'user',
      'cartProductCount'
    ]),
    userNameToShow () {
      if (!this.user.name) {
        return ''
      }
      const allowedLength = 10
      return this.user.name.length < allowedLength + 3 ? this.user.name : this.user.name.slice(0, allowedLength).trim() + '...'
    }
  },
  methods: {
    ...mapActions([
      'logOut'
    ]),
    getBrandLink (item) {
      return {
        name: 'CertainBrand',
        query: {
          name: item
        }
      }
    },
    handleLogOut () {
      this.logOut()
      const routeParent = this.$route.matched[0].name
      if (routeParent === 'User') {
        this.$router.push({ name: 'Home' })
      }
    },
    hideLogIn () {
      this.$refs['login-modal'].hide()
    },
    hideSignUp () {
      this.$refs['singup-modal'].hide()
    }
  },
  components: {
    nested,
    SearchBar,
    LogIn,
    SignUp
  }
}
</script>
