<template>
    <div class="sticky-top">
      <b-navbar toggleable="lg" type="dark" variant="dark">
        <div class="ml-auto d-flex d-inline">
            <b-navbar-brand class="mr-auto d-block d-lg-none">
              <b-nav-form class="">
                <search-bar :id="1"></search-bar>
              </b-nav-form>
            </b-navbar-brand>
            <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
        </div>
        <b-collapse id="nav-collapse" is-nav>
            <b-navbar-brand href="#" :to="{name: 'Home'}" id="logo">Ajraar</b-navbar-brand>
            <b-navbar-nav>
                <b-nav-form>
                    <nested></nested>
                </b-nav-form>
                <b-nav-form>
                    <showBrands></showBrands>
                </b-nav-form>
            </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import nested from '../components/ShowCategory'
import showBrands from '../components/ShowBrands'

export default {
  name: 'Header',
  data () {
    return {
      show1: false
    }
  },
  computed: {
    ...mapGetters([
      'authToken',
      'user',
      'cartProductCount'
    ]),
    userNameToShow () {
      if (!this.user.name) {
        return ''
      }
      const allowedLength = 10
      return this.user.name.length < allowedLength + 3 ? this.user.name : this.user.name.slice(0, allowedLength).trim() + '...'
    }
  },
  methods: {
    ...mapActions([
      'logOut'
    ]),
    getBrandLink (item) {
      return {
        name: 'CertainBrand',
        query: {
          name: item
        }
      }
    },
    handleLogOut () {
      this.logOut()
      const routeParent = this.$route.matched[0].name
      if (routeParent === 'User') {
        this.$router.push({ name: 'Home' })
      }
    },
    hideLogIn () {
      this.$refs['login-modal'].hide()
    },
    hideSignUp () {
      this.$refs['singup-modal'].hide()
    }
  },
  components: {
    nested,
    showBrands
  }
}
</script>
